body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

h1 {
  color: #333;
}

label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

input[type="file"] {
  display: block;
  margin-bottom: 20px;
}

textarea {
  width: 100%;
  width: 500px;
  height: 250px;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: both; 
}

select {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.conclusions-section {
  border-radius: 5px;
  border: 1px solid #4c4c4c;
  padding: 20px;
  width: 60%;
  max-width: 800px;
  margin: 40px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  min-height: 80vh;
  background-color: #f9f9f9;
}

.loader-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #4ea524;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

.loader-text {
  margin-top: 1rem;
  font-size: 1.2rem;
  font-weight: bold;
}

.cancel-button {
  margin-top: 60px; 
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.textbox {
  margin-top: 20px;
}
