.dialog-section {
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 8px;
    background-color: #f9f9f9;
    margin: 20px;
  }
  
  .general-fields {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .general-fields div {
    display: flex;
    flex-direction: column;
  }
  
  .checkbox-container {
    display: flex;
    align-items: center;
  }
  
  .checkbox-container label {
    display: flex;
    align-items: center;
    gap: 5px;
    font-weight: bold;
  }
  
  .dialog-field {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
  }
  
  .dialog-buttons {
    display: flex;
    justify-content: flex-start;
    gap: 10px;
  }
  
  .promt {
    width: 60rem;
    height: 20px;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: both;
  }
  
  .upload-button {
    height: 40px;
    border-radius: 4px;
    min-width: 250px;
    border: 1px solid #ccc;
    display: flex;
    align-items: center;
    justify-content: start;
    padding-left: 3px;
  }
  
  .text {
    width: 47rem;
    height: 20px;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .summarize-button-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .loader-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  
  .loader {
    border: 4px solid #f3f3f3;
    border-top: 4px solid #4ea524;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite;
    margin-bottom: 10px;
  }
  
  .loader-text {
    margin-top: 1rem;
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  .cancel-button {
    margin-top: 20px;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .summary-table {
    border: 1px solid #ccc;
    padding: 20px;
    border-radius: 8px;
    background-color: #f9f9f9;
    margin: 20px;
  }
  
  .summary-table table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .summary-table th,
  .summary-table td {
    border: 1px solid #ddd;
    padding: 12px;
    text-align: left;
  }
  
  .summary-table th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  
  .summary-table tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .summary-table tr:hover {
    background-color: #e9e9e9;
  }
  
  .table-buttons {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
  }
  .summary-table-container {
    max-width: 100%;
    overflow: hidden;
  }
  
  .table-wrapper {
    max-height: 80vh; /* Высота таблицы ограничивается 70% высоты экрана */
    overflow-y: auto; /* Вертикальная прокрутка */
    overflow-x: auto; /* Горизонтальная прокрутка для длинных строк */
    border: 1px solid #ccc;
    border-radius: 4px;
    margin: 20px;
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  th {
    background-color: #f4f4f4; /* Светлый фон для заголовков */
    position: sticky; /* Закрепление заголовков таблицы */
    top: 0; /* Позиция заголовков */
    z-index: 1; /* Заголовки остаются поверх данных при прокрутке */
  }
  
  pre {
    margin: 0;
    font-family: inherit;
  }
  